"use client";
import React, { useEffect, useState } from "react";

export default function Dropdown({
  items = [],
  placeholder = "Select",
  onSelect,
  value,
}) {
  const [open, setOpen] = useState(false);
  const [selected, setSelected] = useState(null);

  useEffect(() => {
    if (value === "" || value === null || value === undefined) {
      setSelected(null);
    } else {
      const found = items.find((item) => item.value === value);
      setSelected(found || null);
    }
  }, [value, items]);

  const handleSelect = (item) => {
    setSelected(item);
    setOpen(false);
    if (onSelect) onSelect(item);
  };

  return (
    <div className="relative w-full">
      <button
        onClick={() => setOpen(!open)}
        className="w-[150px] cursor-pointer flex items-start justify-between rounded-lg text-left outline-none transition"
      >
        <div className="flex items-center gap-2">
          <span
            className={selected ? "text-white" : "text-zinc-400 font-medium"}
          >
            {selected ? selected.label : placeholder}
          </span>
        </div>
      </button>

      {open && (
        <ul className="absolute w-full bg-zinc-900 border border-zinc-800 mt-1 rounded-lg shadow-md z-10">
          {items.map((item) => (
            <li
              key={item.value}
              className={`flex items-center gap-2 p-3  w-[150px] cursor-pointer hover:bg-purple-500/20
                         ${
                           selected?.value === item.value
                             ? "bg-purple-500/30"
                             : ""
                         }`}
              onClick={() => handleSelect(item)}
            >
              <span className="text-zinc-200">{item.label}</span>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
